.jobre{
    background-color: rgba(255,255,255,.8);
    background-image:  url("../../images/jobrecru.jpg");
    background-blend-mode: lighten;
    height: 250px;
    width: 100%;
}

.jobse{
    background-color: rgba(255,255,255,.8);
    background-image:  url("../../images/incu.jpg");
    background-size: cover;
    background-repeat: no-repeat;
    background-blend-mode: lighten;
    height: 250px;
    width: 100%;
}

*{
    font-family: Georgia, 'Times New Roman', Times, serif;
}
.featDiv{
    background-color: rgb(244,245,250);   
}
.viePro{
    padding: 6px 20px;
    border: none;
    background-color: dodgerblue;
    color: white;
    border-radius: 25px;
}

.divStl{
    box-shadow: 10px 10px 15px lightgrey;
}

.feaDiv{
    background-color: rgb(244,245,250);   

    box-shadow: 10px 10px 15px lightgrey;
}
.vieMor{
    padding: 6px 60px;
    border: 2px solid dodgerblue;
    background-color: white;
    color: black;
    border-radius: 25px;
}
.vieMor:hover{
    padding: 6px 60px;
    border: 2px solid white;
    background-color: dodgerblue;
    color: white;
    border-radius: 25px;
}
.viewMore{
    background-color: rgb(244,245,250);   
}

.countDiv{
    background-color: rgb(244,245,250);
}

.small{
    /* padding: 15px; */
    margin: 5px;
    border-radius: 8px;
    cursor: pointer;
    /* color: white; */
    /* background-image: linear-gradient(to bottom right, #3375cc, #58a6f0); */
    transition: .5s;
    border: 2px solid dodgerblue;
    box-shadow: 0 10px 15px lightgrey;
}
.small:hover{
    transform: scale(1.1,1.1);
    background-color: white;
    box-shadow: 0 13px 25px lightgrey;
    border: 2px solid white;
}
